@import "./../../libs/variables.scss";
nav {
    ul {
        display: flex;
        a {
            list-style: none;
            color: inherit;
            width: 50px;
            height: 50px;
            font-size: 26px;
            margin: 0 10px;
            line-height: 55px;
            border-radius: 50%;
            text-align: center;
            background-color: $bg-color;
            transition: $btn-trans;
            li{
                position: relative;
            }

            &:hover, &.active{
                color: $white;
                background-color: $main;
            }
        }

        // @media all and (max-width: 767px) {
